import React, { Component } from 'react';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMore from '@material-ui/icons/ExpandMore';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Typography from '@material-ui/core/Typography';

class Visits extends Component {

    constructor(props) {
        super(props);
        this.state = {
            shopper: this.props.targeting.events.includes("ecommerce_order"),
            expanded: false,
            lastPreset : null
        };



    }

    componentWillReceiveProps({ selectedPreset }) {
        var shopper = false;
        var expanded = this.state.expanded;
        var updateState = false;
        if (selectedPreset === "shopper") {
            shopper = true;
            expanded = true;

            updateState = true;
        }
        if (updateState) {
            this.setState({
                shopper: shopper,
                expanded: expanded
            }, () => {
                this.props.eventChanged('ecommerce_order', this.state.shopper);
            })
        }
    }
    handleChange = name => event => {
        this.setState({ [name]: event.target.checked }, () => {
            this.props.eventChanged('ecommerce_order', this.state.shopper);
        });
    }
    expandedChanged(event, expanded) {
        this.setState({ expanded: expanded });
    }

    render() {
        return (
            <ExpansionPanel className="configPanel" expanded={this.state.expanded} onChange={this.expandedChanged.bind(this)}>
                <ExpansionPanelSummary expandIcon={<ExpandMore />}>
                    <div className="key-column"><Typography >Shoppers</Typography></div>
                    <div className="column" />
                    <div className=""><Typography className="description">Users that did at least one order.</Typography></div>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <FormGroup row>
                        <FormControlLabel
                            control={
                                <Switch
                                    color="primary"
                                    checked={this.state.shopper}
                                    onChange={this.handleChange('shopper')}
                                />
                            }
                            label="Shopper"
                            labelPlacement="start"
                        />

                    </FormGroup>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        )
    }
}

export default Visits;