import React, { Component } from 'react';
import Divider from '@material-ui/core/Divider';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import './App.css';
import Presets from './Presets';
import Config from './Config'

class App extends Component {

  defaultTargeing = {
    devices: [],
    os: [],
    browsers: [],
    visit: "both", // first, returnung, both
    events: [],
    wp_categories: {categories:[], condition: "or"},
    wc_categories: {categories:[], condition: "or"},
  };

  constructor(props) {
    super(props);
    this.state = {
      presets: this.props.presets,
      selectedPreset: "",
      targeting: this.defaultTargeing,
      formValue: JSON.stringify(this.defaultTargeing)
    };
    if (window.webToolsTargeing) {
      this.state['targeting'] = window.tma_webtools_updateTargeting(window.webToolsTargeing)
      this.state['formValue'] = JSON.stringify(window.tma_webtools_updateTargeting(window.webToolsTargeing))
    }
  }

  onPresetSelected(id) {
    this.setState({ selectedPreset: id }, () => {
      // unset selected preset to prevent the child components from wrong functionality because of binding to the current preset
      this.setState({ selectedPreset: null })
    });
  }

  targetingChanged(name, value) {
    let targeting = Object.assign({}, this.state.targeting);
    targeting[name] = value;
    this.setState({ targeting }, () => {
      this.setState({ formValue: JSON.stringify(this.state.targeting) });
    });
  }
  eventChanged(event, added) {
    let targeting = Object.assign({}, this.state.targeting);
    if (added) {
      targeting.events.push(event);
    } else {
      var index = targeting.events.indexOf(event);
      if (index !== -1) {
        targeting.events.splice(index, 1);
      }
    }
    this.setState({ targeting }, () => {
      this.setState({ formValue: JSON.stringify(this.state.targeting) });
    });
  }

  wp_categoryConditionChanged (event) {
    const value = event.target.value;
    let targeting = Object.assign({}, this.state.targeting);
    targeting.wp_categories.condition = value;
    this.setState({ targeting }, () => {
      this.setState({ formValue: JSON.stringify(this.state.targeting) });
    });
  }
  wp_categoryChanged(event) {
    const target = event.target;
    let targeting = Object.assign({}, this.state.targeting);
    if (target.checked) {
      targeting.wp_categories.categories.push(parseInt(target.value));
    } else {
      var index = targeting.wp_categories.categories.indexOf(parseInt(target.value));
      if (index !== -1) {
        targeting.wp_categories.categories.splice(index, 1);
      }
    }
    this.setState({ targeting }, () => {
      this.setState({ formValue: JSON.stringify(this.state.targeting) });
    });
  }

  wc_categoryConditionChanged (event) {
    console.log(event.target.value);
    const value = event.target.value;
    let targeting = Object.assign({}, this.state.targeting);
    targeting.wc_categories.condition = value;
    this.setState({ targeting }, () => {
      this.setState({ formValue: JSON.stringify(this.state.targeting) });
    });
  }
  wc_categoryChanged(event) {
    const target = event.target;
    let targeting = Object.assign({}, this.state.targeting);
    if (target.checked) {
      targeting.wc_categories.categories.push(parseInt(target.value));
    } else {
      var index = targeting.wc_categories.categories.indexOf(parseInt(target.value));
      if (index !== -1) {
        targeting.wc_categories.categories.splice(index, 1);
      }
    }
    this.setState({ targeting }, () => {
      this.setState({ formValue: JSON.stringify(this.state.targeting) });
    });
  }

  handleChange() {
    console.log("changed");
  }

  setTargeting(targeting) {
    let newTargeting = Object.assign({}, this.state.targeting);
    newTargeting = Object.assign(newTargeting, targeting);
    console.log(newTargeting);
    this.setState({ newTargeting }, () => {
      this.setState({ formValue: JSON.stringify(this.state.targeting) });
    });
  }

  render() {
    return (
      <div className="webtoolsQueryEditor">
        <textarea name="tma-webtools-segment-editor-value" value={this.state.formValue} onChange={this.handleChange} className="hidden" />
        <Grid container spacing={8}>
          <Grid item xs={12}>
            <Paper className="paper">
              <Presets config={this.props.config} presetSelected={this.onPresetSelected.bind(this)} />
            </Paper>
            <Divider />
            <Paper className="paper">
              <Config
                selectedPreset={this.state.selectedPreset}
                targetingChanged={this.targetingChanged.bind(this)}
                eventChanged={this.eventChanged.bind(this)}
                targeting={this.state.targeting}
                wp_categoryChanged={this.wp_categoryChanged.bind(this)}
                wp_categoryConditionChanged={this.wp_categoryConditionChanged.bind(this)}
                wc_categoryChanged={this.wc_categoryChanged.bind(this)}
                wc_categoryConditionChanged={this.wc_categoryConditionChanged.bind(this)}
              />
            </Paper>
          </Grid>
        </Grid>
      </div>
    );
  }
}

export default App;