import React, { Component } from 'react';


class WCCategory extends Component {

    constructor(props) {
        super(props);
        this.state = {
            expanded: false,
            categories : this.props.targeting.wc_categories.categories ? this.props.targeting.wc_categories.categories : []
        };
    }

    componentWillReceiveProps({ targeting }) {
        this.setState({ categories: targeting.wc_categories.categories });
    }

    renderChildren(children) {
        if (children && children.length > 0) {
            return (
                <ul className="catlist">
                    {children.map(cat => {
                        return <WCCategory
                            category={cat}
                            key={cat.id}
                            categoryChanged={this.props.categoryChanged} 
                            targeting={this.props.targeting}
                            />
                    })}
                </ul>);
        } else {
            return "";
        }
    }

    render() {
        return (
            <li className="item">
                <label>
                    <input 
                        type="checkbox" 
                        value={this.props.category.id} 
                        checked={this.state.categories.includes(this.props.category.id)}
                        onChange={this.props.categoryChanged}/>
                    &nbsp;{this.props.category.name}
                </label>
                {this.renderChildren(this.props.category.children)}
            </li>
        )
    }
}

export default WCCategory;