import React, { Component } from 'react';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMore from '@material-ui/icons/ExpandMore';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Typography from '@material-ui/core/Typography';

class Visits extends Component {

    constructor(props) {
        super(props);
        this.state = {
            visit: this.props.targeting.visit,
            expanded: false,
        };
    }

    componentWillReceiveProps({ selectedPreset }) {
        var visit = "first";
        var expanded = this.state.expanded;
        var updateState = false;
        if (selectedPreset === "firstvisit") {
            visit = "first";
            expanded = true;

            updateState = true;
        } else if (selectedPreset === "returning") {
            visit = "returning";
            expanded = true;

            updateState = true;
        }
        if (updateState) {
            this.setState({
                visit: visit,
                expanded: expanded
            }, () => {
                this.props.targetingChanged('visit', this.state.visit);
            })
        }
    }
    handleChange = (name, value) => event => {
        this.setState({ [name]: value }, () => {
            this.props.targetingChanged('visit', this.state.visit);
        });
    }
    expandedChanged(event, expanded) {
        this.setState({ expanded: expanded });
    }

    render() {
        return (
            <ExpansionPanel className="configPanel" expanded={this.state.expanded} onChange={this.expandedChanged.bind(this)}>
                <ExpansionPanelSummary expandIcon={<ExpandMore />}>
                    <div className="key-column"><Typography >Visitor</Typography></div>
                    <div className="column" />
                    <div className=""><Typography className="description">First or returning visitors.</Typography></div>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <FormGroup row>
                        <FormControlLabel
                            value="first"
                            control={
                                //<Radio color="primary" />
                                <Switch
                                    color="primary"
                                    checked={this.state.visit === "first"}
                                    onChange={this.handleChange('visit', 'first')}
                                />
                            }
                            label="FirstVisit"
                            labelPlacement="start"
                        />
                        <FormControlLabel
                            value="returning"
                            control={
                                //<Radio color="primary" />
                                <Switch
                                    color="primary"
                                    checked={this.state.visit === "returning"}
                                    onChange={this.handleChange('visit', 'returning')}
                                />
                            }
                            label="Returning"
                            labelPlacement="start"
                        />
                        <FormControlLabel
                            value="both"
                            control={
                                //<Radio color="primary" />
                                <Switch
                                    color="primary"
                                    checked={this.state.visit === "both"}
                                    onChange={this.handleChange('visit', 'both')}
                                />
                            }
                            label="Both"
                            labelPlacement="start"
                        />

                    </FormGroup>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        )
    }
}

export default Visits;