import React, { Component } from 'react';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMore from '@material-ui/icons/ExpandMore';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Typography from '@material-ui/core/Typography';

class Devices extends Component {

    constructor(props) {
        super(props);
        this.state = {
            mobile: this.props.targeting.devices.includes("mobile"),
            tablet: this.props.targeting.devices.includes("tablet"),
            computer: this.props.targeting.devices.includes("computer"),
            console: this.props.targeting.devices.includes("console"),
            dmr: this.props.targeting.devices.includes("dmr"),
            wearable: this.props.targeting.devices.includes("wearable"),
            expanded: false,
        };

        this.devices = [
            { id: "mobile", name: "Mobile" },
            { id: "tablet", name: "Tablet" },
            { id: "computer", name: "Computer" },
            { id: "console", name: "Game Console" },
            { id: "dmr", name: "Digital media receiver" },
            { id: "wearable", name: "Wearable computer" },
        ];

    }
    componentWillReceiveProps({ selectedPreset }) {
        if (selectedPreset === "mobile" || selectedPreset === "iphone" || selectedPreset === "android") {
            this.setState({ mobile: true, expanded: true }, this.updateParent);
        } else if (selectedPreset === "tablet") {
            this.setState({ tablet: true, expanded: true }, this.updateParent);
        } else if (selectedPreset === "computer") {
            this.setState({ computer: true, expanded: true }, this.updateParent);
        } else if (selectedPreset === "console") {
            this.setState({ console: true, expanded: true }, this.updateParent);
        } else if (selectedPreset === "dmr") {
            this.setState({ dmr: true, expanded: true }, this.updateParent);
        } else if (selectedPreset === "wearable") {
            this.setState({ wearable: true, expanded: true }, this.updateParent);
        }
    }

    updateParent() {
        var selectedDevices = [];
        this.devices.forEach(element => {
            if (this.state[element.id]) {
                selectedDevices.push(element.id);
            }
        });

        this.props.targetingChanged("devices", selectedDevices);
    }
    handleChange = name => event => {
        this.setState({ [name]: event.target.checked }, this.updateParent);
    }

    expandedChanged(event, expanded) {
        this.setState({ expanded: expanded });
    }

    render() {
        return (
            <ExpansionPanel className="configPanel" expanded={this.state.expanded} onChange={this.expandedChanged.bind(this)}>
                <ExpansionPanelSummary expandIcon={<ExpandMore />}>
                    <div className="key-column"><Typography >Device</Typography></div>
                    <div className="column" />
                    <div className=""><Typography className="description">Targeting by used device.</Typography></div>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <FormGroup row>
                        {this.devices.map(device => {
                            return <FormControlLabel
                                key={device.id}
                                control={
                                    <Switch
                                        color="primary"
                                        checked={this.state[device.id]}
                                        onChange={this.handleChange(device.id)}
                                    />
                                }
                                label={device.name}
                                labelPlacement="start"
                            />
                        })}
                    </FormGroup>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        )
    }
}

export default Devices;