import React, { Component } from 'react';
import Button from '@material-ui/core/Button'
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

class Presets extends Component {

    onPresetSelected(id) {
        this.props.presetSelected(id);
    }

    render() {
        return (
            <Grid container item spacing={8}>
                <Grid container item xs={12}>
                    <Grid item xs={12} className="center presetHeading">
                        <Typography variant="title">Device</Typography>
                        <Typography variant="subtitle2">Device specific targeting</Typography>
                    </Grid>
                    <Grid item container xs={12}>
                        {this.props.config.presets.device.map(element =>
                            <Grid item xs={2} key={element.id}>
                                <Card className="preset-{element.id} presets" >
                                    <CardContent>
                                        <Typography variant="h6">{element.name}</Typography>
                                        <FontAwesomeIcon icon={element.icon} size="3x"/>
                                    </CardContent>
                                    <CardActions>
                                        <Button size="small" onClick={this.onPresetSelected.bind(this, element.id)}>Use</Button>
                                    </CardActions>
                                </Card>
                            </Grid>
                        )}
                    </Grid>
                </Grid>
                <Grid container item xs={12}>
                    <Grid item xs={12} className="center presetHeading">
                        <Typography variant="title">Visitor</Typography>
                        <Typography variant="subtitle2">Visitor specific targeting</Typography>
                    </Grid>
                    <Grid item container xs={12}>
                        {this.props.config.presets.visitor.map(element =>
                            <Grid item xs={2} key={element.id}>
                                <Card className="preset-{element.id} presets" >
                                    <CardContent>
                                        <Typography variant="h6">{element.name}</Typography>
                                        <FontAwesomeIcon icon={element.icon} size="3x"/>
                                    </CardContent>
                                    <CardActions>
                                        <Button size="small" onClick={this.onPresetSelected.bind(this, element.id)}>Use</Button>
                                    </CardActions>
                                </Card>
                            </Grid>
                        )}
                    </Grid>
                </Grid>
            </Grid>
        )
    }
}

export default Presets;