window.webtoolsQueryEditor = function () {
};
window.webtoolsQueryEditor.plugins = [];
window.webtoolsQueryEditor.registerPlugin = function (plugin) {
    window.webtoolsQueryEditor.plugins.push(plugin);
}
window.webtoolsQueryEditor.registerPlugin({
    id : "test",
    render : function (props) {
        return <div className="holger">Test</div>
    }
});

window.tma_webtools_updateTargeting  = function (targeting) {
    if (!targeting["os"]){
        targeting.os = [];
    }
    if (!targeting["events"]){
        targeting.events = [];
    }
    if (!targeting["devices"]){
        targeting.devices = [];
    }
    if (!targeting["browsers"]){
        targeting.browsers = [];
    }
    if (!targeting["wp_categories"]){
        targeting.wp_categories = {};
        targeting.wp_categories.categories = [];
        targeting.wp_categories.condition = 'or';
    }
    if (!targeting["wc_categories"]){
        targeting.wc_categories = {};
        targeting.wc_categories.categories = [];
        targeting.wc_categories.condition = 'or';
    }
    return targeting;
}

ReactDOM.render(<App config={config} />, document.getElementById('webtoolsQueryEditor'));


