import React, { Component } from 'react';

import Devices from './configs/Devices'
import Visits from './configs/Visits'
import Shopper from './configs/Shopper'
import Browser from './configs/Browser'
import OperatingSystem from './configs/OperatingSystem'
import WPCategories from './configs/WPCategories'
import WCCategories from './configs/WCCategories'

class Config extends Component {

    render() {
        return (
            <div>
                <Devices selectedPreset={this.props.selectedPreset} targetingChanged={this.props.targetingChanged} targeting={this.props.targeting} />
                <OperatingSystem selectedPreset={this.props.selectedPreset} targetingChanged={this.props.targetingChanged} targeting={this.props.targeting} />
                <Visits selectedPreset={this.props.selectedPreset} targetingChanged={this.props.targetingChanged} targeting={this.props.targeting}/>
                <Browser selectedPreset={this.props.selectedPreset} targetingChanged={this.props.targetingChanged} targeting={this.props.targeting}/>
                <Shopper selectedPreset={this.props.selectedPreset} eventChanged={this.props.eventChanged} targeting={this.props.targeting}/>
                <WPCategories 
                    selectedPreset={this.props.selectedPreset} 
                    categoryChanged={this.props.wp_categoryChanged} 
                    conditionChanged={this.props.wp_categoryConditionChanged}
                    targeting={this.props.targeting}/>
                <WCCategories 
                    selectedPreset={this.props.selectedPreset} 
                    categoryChanged={this.props.wc_categoryChanged} 
                    conditionChanged={this.props.wc_categoryConditionChanged}
                    targeting={this.props.targeting}/>
                {/*window.webtoolsQueryEditor.plugins.map((plugin => plugin.render()))*/}
            </div>
        )
    }
}

export default Config;