import React, { Component } from 'react';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMore from '@material-ui/icons/ExpandMore';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Typography from '@material-ui/core/Typography';

class OperatingSystem extends Component {

    constructor(props) {
        super(props);
        this.state = {
            windows: this.props.targeting.os.includes("windows"),
            ios: this.props.targeting.os.includes("ios"),
            android: this.props.targeting.os.includes("android"),
            linux: this.props.targeting.os.includes("linux"),
            mac: this.props.targeting.os.includes("mac"),
            expanded: false,
        };

        this.operation_systems = [
            { id: "windows", name: "Windows" },
            { id: "ios", name: "iOS" },
            { id: "android", name: "Android" },
            { id: "linux", name: "Linux" },
            { id: "mac", name: "Mac" },
        ];

    }
    componentWillReceiveProps({ selectedPreset }) {
        if (selectedPreset === "iphone") {
            this.setState({ ios: true, expanded: true }, this.updateParent);
        } else if (selectedPreset === "android") {
            this.setState({ android: true, expanded: true }, this.updateParent);
        }
    }

    updateParent() {
        var selectedDevices = [];
        this.operation_systems.forEach(element => {
            if (this.state[element.id]) {
                selectedDevices.push(element.id);
            }
        });

        this.props.targetingChanged("os", selectedDevices);
    }
    handleChange = name => event => {
        this.setState({ [name]: event.target.checked }, this.updateParent);
    }

    expandedChanged(event, expanded) {
        this.setState({ expanded: expanded });
    }

    render() {
        return (
            <ExpansionPanel className="configPanel" expanded={this.state.expanded} onChange={this.expandedChanged.bind(this)}>
                <ExpansionPanelSummary expandIcon={<ExpandMore />}>
                    <div className="key-column"><Typography >OperatingSystem</Typography></div>
                    <div className="column" />
                    <div className=""><Typography className="description">Targeting by users operating system.</Typography></div>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <FormGroup row>
                        {this.operation_systems.map(device => {
                            return <FormControlLabel
                                key={device.id}
                                control={
                                    <Switch
                                        color="primary"
                                        checked={this.state[device.id]}
                                        onChange={this.handleChange(device.id)}
                                    />
                                }
                                label={device.name}
                                labelPlacement="start"
                            />
                        })}
                    </FormGroup>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        )
    }
}

export default OperatingSystem;