/*global ajaxurl*/

import React, { Component } from 'react';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Typography from '@material-ui/core/Typography';
import CustomScroll from 'react-custom-scroll';
import 'react-custom-scroll/dist/customScroll.css';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';

import WPCategory from '../components/WPCategory'

class WPCategories extends Component {

    constructor(props) {
        super(props);
        this.state = {
            //categories: this.props.targeting.wp_categories.categories ? this.props.targeting.wp_categories.categories : [],
            categories: [],
            expanded: false,
            condition: this.props.targeting.wc_categories.condition
        };

        this.getCategories();
    }
    async getCategoryTree(parentId, cats) {
        var children = [];

        for (const cat of cats) {
            if (cat.parent === parentId) {
                var child = {};
                child.id = cat.term_id;
                child.name = cat.name;

                let response = await window.fetch(ajaxurl + "?action=tma_categories&parent=" + cat.term_id);
                let result = await response.json();
                child.children = await this.getCategoryTree(cat.term_id, result);

                children.push(child);
            }
        }


        return children;
    }
    componentDidMount() {
        this.getCategories();
    }

    getCategories() {
        if (window.webToolsWPCategories) {
            this.setState({ categories: window.webToolsWPCategories });
        } else if (typeof ajaxurl != "undefined") {
            window.fetch(ajaxurl + "?action=tma_categories&parent=0").then(response => response.json()).then(response => {
                this.getCategoryTree(0, response).then(categories => {
                    this.setState({ categories: categories });
                });
            });
        }
    }

    expandedChanged(event, expanded) {
        this.setState({ expanded: expanded });
    }

    render() {
        return (
            <ExpansionPanel className="configPanel" onChange={this.expandedChanged.bind(this)}>
                <ExpansionPanelSummary expandIcon={<ExpandMore />}>
                    <div className="key-column"><Typography >WordPress Categories</Typography></div>
                    <div className="column" />
                    <div className=""><Typography className="description">Targeting by viewed categories.</Typography></div>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <div className="tma_cats">
                        <CustomScroll heightRelativeToParent="100%">
                            <ul className="catlist">
                                {this.state.categories.map(category => {
                                    return <WPCategory
                                        key={category.id}
                                        category={category}
                                        targeting={this.props.targeting}
                                        categoryChanged={this.props.categoryChanged}
                                    />
                                })}
                            </ul>
                        </CustomScroll>
                    </div>
                    <div className="tma_cats_condition">
                    <FormControl>
                        <Select
                            value={this.props.targeting.wp_categories.condition}
                            onChange={this.props.conditionChanged}
                        >
                            <MenuItem value={'or'}>OR</MenuItem>
                            <MenuItem value={'and'}>AND</MenuItem>
                        </Select>
                        <FormHelperText>Condition</FormHelperText>
                    </FormControl>
                </div>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        )
    }
}

export default WPCategories;