import React, { Component } from 'react';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMore from '@material-ui/icons/ExpandMore';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Typography from '@material-ui/core/Typography';

class Browser extends Component {

    constructor(props) {
        super(props);
        this.state = {
            chrome: this.props.targeting.browsers.includes("chrome"),
            ie: this.props.targeting.browsers.includes("ie"),
            firefox: this.props.targeting.browsers.includes("firefox"),
            edge: this.props.targeting.browsers.includes("edge"),
            safari: this.props.targeting.browsers.includes("safari"),
            opera: this.props.targeting.browsers.includes("opera"),

            expanded: false
        };

        this.browsers = [
            { id: "chrome", name: "Chrome" },
            { id: "ie", name: "Internet Explorer" },
            { id: "firefox", name: "Firefox" },
            { id: "edge", name: "Microsoft Edge" },
            { id: "safari", name: "Safari" },
            { id: "opera", name: "Opera" },
        ];

    }
    componentWillReceiveProps({ selectedPresets }) {
    }
    updateParent() {
        var selectedBrowsers = [];
        this.browsers.forEach(element => {
            if (this.state[element.id]) {
                selectedBrowsers.push(element.id);
            }
        });

        this.props.targetingChanged("browsers", selectedBrowsers);
    }
    handleChange = name => event => {
        this.setState({ [name]: event.target.checked }, this.updateParent);
    }
    
    expandedChanged(event, expanded) {
        this.setState({ expanded: expanded });
    }

    render() {
        return (
            <ExpansionPanel className="configPanel" onChange={this.expandedChanged.bind(this)}>
                <ExpansionPanelSummary expandIcon={<ExpandMore />}>
                    <div className="key-column"><Typography >Browser</Typography></div>
                    <div className="column" />
                    <div className=""><Typography className="description">Select users by browser engine.</Typography></div>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <FormGroup row>
                        {this.browsers.map(browser => {
                            return <FormControlLabel
                                key={browser.id}
                                control={
                                    <Switch
                                        color="primary"
                                        checked={this.state[browser.id]}
                                        onChange={this.handleChange(browser.id)}
                                    />
                                }
                                label={browser.name}
                                labelPlacement="start"
                            />
                        })}
                    </FormGroup>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        )
    }
}

export default Browser;